.btn_animate
    display: flex
    justify-content: center
    align-items: center
    position: relative
    overflow: hidden
    border: 2px solid black
    transition: 0.3s
    font-family: 'Closer-SemiBold', sans-serif
    span
        position: relative
        z-index: 2
        color: black
    &::before, &::after
        content: ''
        position: absolute
        left: 0
        width: 100%
        height: 100%
        transition: 0.3s

    &::before
        top: -100%
    &::after
        top: 100%
    &:hover
        border: none
        span
            color: white
        &::before, &::after
            top: 0
            background-color: currentColor

           
            
.fizruk
    .btn_animate
        &:hover
            border: none
            color: white
            &::before, &::after
                background-color: $fizruk-clr
                
.direct
    .btn_animate
        &:hover
            border: none
            color: white
            &::before, &::after
                background-color: $direct-clr

.green-build
    .btn_animate
        &:hover
            border: none
            color: white
            &::before, &::after
                background-color: $green-build-clr
        
